<template>
  <div>
<div>
            <b-card>
            <b-card-title>Question:{{blog.question}}</b-card-title>
              <b-card-text>
                <b-list-group flush>
                  <b-list-group-item
                    >Option 1:{{ blog.optionOne }}</b-list-group-item
                  >
                  <b-list-group-item
                    >Option 2:{{ blog.optionTwo }}</b-list-group-item
                  >
                  <b-list-group-item
                    >Option 2:{{ blog.optionThree }}</b-list-group-item
                  >
                  <b-list-group-item
                    >Option 2:{{ blog.optionFour }}</b-list-group-item
                  >
                </b-list-group>
              </b-card-text>
            
            <b-card-footer
              >Correct:Option {{ blog.correctAnswer }}</b-card-footer
            >
            </b-card>
          </div>

     
     
  </div>

 
</template>
<script>
import db from './firebaseinit'
export default {
  data(){
    return {
      id:this.$route.params.id,
      blog:{}
    }
  },
  created(){
    db.collection("Question").get().then((querySnapshot) =>{
      
    querySnapshot.forEach((doc)=> {
      let alpha = doc.data()
alpha.question.id=doc.id
console.log(alpha.question)

      
        this.blog=alpha.question;
        
        
    });
});
  }
}
</script>


