<template><div>
    <div id="show-blog">
      <div v-for="(xray, index) in blogData" :key="index" class="single-blog">
        <router-link v-bind:to="'/blog/' + xray.id">
          <div>
            <b-card>
            <b-card-title>Question:{{xray.question}}</b-card-title>
              <b-card-text>
                <b-list-group flush>
                  <b-list-group-item
                    >Option 1:{{ xray.optionOne }}</b-list-group-item
                  >
                  <b-list-group-item
                    >Option 2:{{ xray.optionTwo }}</b-list-group-item
                  >
                  <b-list-group-item
                    >Option 2:{{ xray.optionThree }}</b-list-group-item
                  >
                  <b-list-group-item
                    >Option 2:{{ xray.optionFour }}</b-list-group-item
                  >
                </b-list-group>
              </b-card-text>
            
            <b-card-footer
              >Correct:Option {{ xray.correctAnswer }}</b-card-footer
            >
            </b-card>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import db from "./firebaseinit";

export default {
  components: {},
  data() {
    return {
      blogData: [],
      search: "",
    };
  },
  created() {
    db.collection("Question")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let alpha = doc.data();
          alpha.question.id = doc.id;
          console.log(alpha.question);

          this.blogData.push(alpha.question);
        });
      });
  },
};
</script>
<style scoped>
#show-blog {
  max-width: 800px;
  margin: 0px auto;
}
#show-blog *{
  text-decoration-line:none ;
  color:black;

}

</style>
