<template>
<div>

  <div class="bg-light text-dark dabba">
    <div class="container-fluid">
      <b-form @submit="onSubmit" @reset="onReset" v-if="show">
        <b-form-group
          id="input-group-0"
          label="Question"
          label-for="input-0"
          class="font-weight-bold"
        >
          <b-form-input
            id="input-0"
            v-model="model.question"
            type="text"
            required
            placeholder="Enter Question"
          ></b-form-input>
        </b-form-group>
  <b-form-group
          id="input-group-1"
          label="Option 1"
          label-for="input-1"
          class="font-weight-bold"
        >
          <b-form-input
            id="input-1"
            v-model="model.optionOne"
            type="text"
            required
            placeholder="Enter Answer"
          ></b-form-input>
        </b-form-group>
       
        <b-form-group
          id="input-group-2"
          label="Option 2"
          label-for="input-2"
          class="font-weight-bold"
        >
          <b-form-input
            id="input-2"
            v-model="model.optionTwo"
            type="text"
            required
            placeholder="Enter Answer"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="input-group-3"
          label="Option 3"
          label-for="input-3"
          class="font-weight-bold"
        >
          <b-form-input
            id="input-3"
            v-model="model.optionThree"
            type="text"
            required
            placeholder="Enter Answer"
          ></b-form-input>
        </b-form-group>
        <b-form-group
          id="input-group-4"
          label="Option 4"
          label-for="input-4"
          class="font-weight-bold"
          
        >
          <b-form-input
            id="input-4"
            v-model="model.optionFour"
            type="text"
            required
            placeholder="Enter Answer"
          ></b-form-input>
        </b-form-group>
          <b-form-group
            id="input-group-5"
            label="Select Correct Answer:"
            label-for="input-5"
            class="font-weight-bold"
          >
            <b-form-select
              id="input-5"
              v-model="model.correctAnswer"
              :options="model.foods"
              required
            ></b-form-select>
          </b-form-group>
     

      

        
        

       

        <b-card class="mt-3 font-weight-bold" header="Preview Question">
          
          <p>Question:{{ model.question }}</p>
          <p>Option One :{{ model.optionOne }}</p>
          <p>Option Two :{{ model.optionTwo }}</p>
          <p>Option Three :{{ model.optionThree }}</p>
          <p>Option Four :{{ model.optionFour }}</p>
          <p>Correct Answer: {{ model.correctAnswer }}</p>
          <b-card-footer>   <b-button type="submit" block variant="primary"  >Submit Question</b-button>
      <b-button type="reset" block variant="danger">Reset</b-button></b-card-footer>
       
        </b-card>

      
        
       </b-form>
    </div>
  </div>
  </div>
</template>

<script>
import db from "./firebaseinit";

export default {
  components:{
  },
  data: function() {
    return {
       show : true,
      model: {
        question: "",
        optionOne: "",
        optionTwo: "",
        optionThree: "",
        optionFour: "",
        correctAnswer: "",
        foods: [
          { text: "Select One", value: null },
          
          { text: "Option One", value: "one" },
          { text: "Option Two", value: "two" },
          { text: "Option Three", value: "three" },
          { text: "Option  Four", value: "four" },
          
        ],
        food: null,
      },
      submit: false,
    };
  },
  methods: {
     onSubmit(evt) {
        evt.preventDefault()
        alert(JSON.stringify(this.model))
         db.collection("Question")
        .add({
          question: this.model,
        })
        .then((docRef) => {
          console.log("Document written with ID: ", docRef.id);
          this.model = {
            question: "",
            optionOne: "",
            optionTwo: "",
            optionThree: "",
            optionFour: "",
            
          };
          this.submit = true;
        })
        .catch(function(error) {
          console.error("Error adding document: ", error);
        });
      },

    onReset(evt) {
        evt.preventDefault()
        // Reset our form values
        this.model.question ="",
         this.model.optionOne= "",
         this.model.optionTwo= "",
         this.model.optionThree ="",
         this.model.optionFour= "",
         this.model.correctAnswer="",
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      },
    
   
  },
};
</script>

<style scoped>
.dabba {
  max-width: 750px;
  margin: 0 auto;
  margin-top: 30px;
  border: 1px solid green;
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
